.overlay
    height: 100vh
    width: 100vw
    background: rgba(255,255,255,0.7)
    position: fixed
    top: 0
    z-index: 1
    display: none

    .close
        position: fixed
        top: 10px
        right: 10px
        width: 6vh
        height: 6vh
        display: flex
        justify-content: center
        align-items: center
        padding: 2vh
        p
            transform: translate(0, 2px)
    
    .instructions
        text-align: center
        margin-top: 5vh

    .interface-wrapper
        width: 60vw 
        border: solid black $stroke-weight 
        border-radius: 5vh
        background-color: white
        margin: auto
        display: flex
        flex-wrap: wrap
        justify-content: center 

.robo-chooser-overlay
    .interface-wrapper
        padding: 2vh
        display: flex
        flex-wrap: wrap
        justify-content: center
        overflow-y: auto
        overflow-x: hidden
        .robo-chooser-button
            margin: 1vh
            display: flex
            flex-direction: column
            max-height: 20vh
            width: 17vh
            border: solid black $stroke-weight
            border-radius: 5vh
            .robo-icon
                height: 70% 
                transform: scale(1.5, 1.5)
            .robo-name
                margin: 0
                margin-top: 2.5vh
            .info-text
                font-size: 2.5vh
                width: 12vh
                text-align: left
                line-height: 1.2
                margin-left: 1vh
                margin-top: 1vh
                padding-left: 2vh
                margin-block-end: 0
            .button-wrapper
                margin-top: 1vh
                justify-content: space-around
                width: 100%
                .claim-button
                    font-size: 3vh
                .wave-button 
                    display: flex
                    align-items: center
                    justify-content: center
                    .wave-emoji
                        width: 1vh
                        transform-origin: center
                        transform: scale(5) translate(.07vh, -.33vh)
            .button
                border-radius: 2.5vh
                padding-top: .5vh
                padding-bottom: .5vh
                height: 3vh

        .active
            // flex-grow: 1
            flex-wrap: wrap
            flex-direction: row
            width: 30vh
            &:hover
                transform: scale(1.00)
        
            &:active
                transform: scale(1.00)
            


.save-load-overlay
    flex-direction: column
    justify-content: center
    align-items: center
    .instructions
        margin-top: 15vh
    .interface-wrapper
        height: 30vh
        width: 60vh
        margin-top: 0
        padding: 5vh
        display: flex
        justify-content: space-between
        .button
            height: auto
            width: auto
            .icon
                min-width: 20vh
            p
                margin: 0

.help-overlay
    .interface-wrapper
        .help-text 
            width: 100%
            padding-left: 2vh
            padding-right: 2vh
            text-align: center

.drawing-overlay
    .interface-wrapper
        border: none
        background-color: transparent
        width: 45vh
        height: 60vh
        .canvas-panel            
            border: $stroke-weight solid black
            border-radius: 1.5rem
            min-width: 100%
            display: flex
            flex-direction: column
            align-items: center
            background-color: #FFFFFF
            box-sizing: border-box // WHY do i need this if i have border box defined in html
        
            .canvas-wrapper
                width: 40vh !important
                height: 40vh !important
                margin-top: 2.2vh
                border: $stroke-weight solid black
                border-radius: 1.5rem
                box-sizing: border-box
                position: relative

                .indicator
                    width: 9%
                    height: 9%
                    position: absolute
                    top: 4%
                    left: 4%
                    border-radius: 50%
                    opacity: 1.0

                .path-drawing-canvas              
                    box-sizing: border-box

            .draw-icon
                width: 10vh
                //disables selecting
                pointer-events: none
                -webkit-user-drag: none
                
        .interface-buttons
            width: 100%
            display: flex
            justify-content: flex-end
            flex-direction: row
            margin-top: 1vh
            img 
                transform: scale(1.3,1.3)


            .interface-button
                border: $stroke-weight solid black
                border-radius: 2.5vh
                padding: .3rem
                width: 5vh
                height: 5vh
                margin-left: 1vh
                //min-width: 8vh
                //min-height: 8vh
            
            .delete
                margin-left: 0
                margin-right: auto
                transition: 0.25s ease-in-out
                &:active
                    background: #EA5A47

@keyframes blinking
    50%
        opacity: 0.0
        

@keyframes fadeout
    100%
        opacity: 0.0
        